.hero {
  padding-top: 64px;
  padding-bottom: 100px;
  background-image: url(./img/borderlands_3_happy_birthday_4k_hd.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  height: 100vh;
}
.hero .container {
  position: relative;
  padding: 16px;
  max-width: 960px;
  width: 100%;
  margin: 100px auto;
  background: transparent;
  backdrop-filter: blur(20px);
  border: 2px solid #fff;
  border-radius: 16px;
  color: #fff;
}
.hero .container .infos {
  display: flex;
  justify-content: space-between;
}
.hero .container .link-confirma-presenca {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-right: 0px;
  padding: 8px;
  border-radius: 16px;
  color: #000;
  background-color: yellow;
  transition: transform 0.9s;
}
.hero .container .link-confirma-presenca:hover {
  transform: scale(1.1);
}
.hero::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  content: "";
  opacity: 0.7;
}
.hero #contador {
  display: block;
}
.hero__text {
  margin-top: 64px;
  margin-bottom: 64px;
  font-size: 40px;
  line-height: 56px;
  font-weight: 800;
  text-align: center;
}
.hero .informacoes__lista {
  list-style: none;
}
@media (max-width: 640px) {
  .hero {
    padding: 40px 0;
  }
  .hero__text {
    font-size: 22px;
    line-height: 24px;
    margin: 24px 0;
  }
  .hero .container {
    max-width: 90%;
  }
  .hero .container .infos {
    display: block;
  }
  .hero .container .link-confirma-presenca {
    margin: 0 auto;
    max-width: 200px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  background: linear-gradient(#fff, orange, yellow);
}

@media (max-width: 640px) {
  .container {
    padding: 8px;
    max-width: 80%;
  }
}
@media (max-width: 1024px) {
  .container {
    max-width: 90%;
  }
}

.container-content {
  display: flex;
  justify-content: space-between;
  margin: 48px auto;
  max-width: 600px;
}
.container-content .content-left__title {
  font-size: 32px;
  margin-bottom: 8px;
}
.container-content .content-left__list {
  margin-top: 16px;
  list-style: none;
}
.container-content .content-left__list__item {
  padding: 8px;
}
.container-content .content-right {
  display: flex;
  align-items: center;
}
.container-content .content-right__img {
  height: 100px;
}